<template>
  <div class="search" >

  <div class="container">
    <div class="weather-side">
      <div class="weather-gradient">
        <div class="date-container">
          <h2 class="location">Options</h2>
            <button 
              @click="switchComponent('weather')"
              :disabled="currentComp==='weather'"
              id="options_btn"></button>

            <form v-on:submit.prevent="selectCity">
              <div id="units">
                <div> Units </div> 
                <input type="radio" id="metric" name="units" value="metric"><label for="metric">C</label>
                <input type="radio" id="imperial" name="units" value="imperial"><label for="imperial">F</label>
                </div>

              <div id="showforecast">
                <div> 5 day Forecast </div> 
                <input type="radio" id="forecastyes" name="forcast" value="yes"><label for="yes">Yes</label>
                <input type="radio" id="forecastno" name="forecast" value="no"><label for="no">No</label>
                </div>

              <div id="searchCity">
                <input id=city type="search" placeholder="Enter city name, country" size="30" v-model="city"  > 
                <button v-on:click="selectCity()" value="Search">Search</button>
              </div>

              <div id="cityDetails">
                <div> Country: {{ this.$store.state.weather.country }} </div> 
              </div>
              <div id="coords">
                <div> Lat:</div> {{ this.$store.state.weather.coords.lat }} 
                <div> Lon:</div> {{ this.$store.state.weather.coords.lon }} 
              </div>

            </form>


            <div id="saveOptions">
              <button id="save"  @click="switchComponent('weather')" :disabled="currentComp==='weather'"
> Save </button>
            </div>


          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { bus } from '../main.js'

export default{

  data(){
    return {city: ''}
  },
  props: {
    currentComp: {
      type: String,
      required: true
    }
  },
  created(){
    this.city = this.$store.state.inputQuery;
  },
  methods: {
  switchComponent(comp) {
    bus.$emit('switchComp', comp);
  },
  getCity(){
      this.city = this.store().inputQuery;
  },
  browserGeoLocation(){
      return new Promise( (resolve) => {
        if ( !navigator.geolocation ) {
          this.$store.dispatch('appStatus', {
            status: 'error',
            message: 'Cannot detect your location. Please search for your city'
          })
        return
      }

      navigator.geolocation.getCurrentPosition(position => {

      console.log("search lat=" + position.coords.latitude);
      console.log("search long=" + position.coords.longitude);

      this.$store.dispatch('coords', {
         lat: position.coords.latitude,
         lon: position.coords.longitude
      })
      resolve()
    }, err => {
       console.log("Unable to autodetect location. Please enter city manually")
       this.gettingLocation = false;
       this.errorStr = err.message;
       this.selectCity('London')
    }, { enableHighAccuracy: true})
   }) 
  },
  getBrowserLocation(){
    console.log("getBrowserLocation()...");
    this.browserGeoLocation().then( () => {

//  this.$store.dispatch('geocode', 'default').then(() => {
    this.$store.dispatch('weather', 'default')
        .then(() => this.$store.dispatch('appStatus', {state: 'loaded'}))
    })


//  })
  },  

  selectCity(defCity) {
      console.log("Entered city = " + this.city)
      let query = (this.city.length === 0) ? 'London' : this.city;
      console.log("Searching for : " + query + " def city="+defCity);

       this.$store.dispatch('inputQuery', query)
       console.log("setting app status in selectCity");
       this.$store.dispatch('appStatus', {state: 'loading'})
       console.log("getting weather in selectCity");
       this.$store.dispatch('weather')
            .then(() => this.$store.dispatch('appStatus', {state: 'loaded'}))
    }
  }
}
</script>

