import Vue from 'vue'
import Vuex from 'vuex'
//import axios from 'axios'
//import { DateTime } from 'luxon'

Vue.use(Vuex)

export default ({
  state: {
      status: {state:'loaded', message:null},
      data: {
        temperature: 0,
        feelslike: null,
        windspeed: null,
//        sunrise: null,
//        sunset: null,
        humidity: null,
        weather: null,
        desc: null,
//        tz: null,
        },
      weather: { coords:{}},
      location: {},
      coords: {
        lat: 0,
        lon:0 
      },
      input: null,
      locationIcon: 'search',
      description:null ,
      inputQuery: this.getInputQuery()

  },
  mutations: {
    initializeStore(state){
      if (localStorage.getItem('state')){
        this.replaceState( Object.assign(state.JSON.parse(localStorage.getItem('state'))));
      }
    },
  setAppStatus: (state, appStatus) => {
      state.status.state = appStatus.state
      state.status.message = appStatus.message
      localStorage.setItem('status', JSON.stringify(state.status));
    },

    setCoordinates: (state, coordinates) => {
      state.coords.lat= coordinates.lat
      state.coords.lon= coordinates.lon
      localStorage.setItem('coords', JSON.stringify(state.coords));
    },


    setGeocode: (state, geocode) => {
      state.geocode = geocode
      state.coords.lat= geocode.geometry.location.lat
      state.coords.lon= geocode.geometry.location.lng
      localStorage.setItem('coords', JSON.stringify(state.coords));
    },
    setInputQuery: (state, inputQuery) => {
      state.inputQuery = inputQuery
      
      localStorage.setItem('inputQuery', JSON.stringify(state.inputQuery));
    },
  
    setWeather: (state, weather) => {
      state.weather = weather
    }



  },
  actions: {
  appStatus ({commit}, appStatus) {
      commit('setAppStatus', appStatus)
    },

    coords ({commit}, coordinates) {
      commit('setCoordinates', coordinates)
    },
    inputQuery ({commit}, inputQuery) {
      commit('setInputQuery', inputQuery)
    },

    getInputQuery(){
      let q = localStorage.getItem('inputQuery');
      if (q === '' || q === null) 
        q = 'London'
      else 
        q.slice(1,-1)

      console.log("inputQuery=" + q)
      return q;
    },

   geocode ({commit, state}, type) {
     /*    return new Promise((resolve, reject) => {
        //        let query
        console.log("reject="+ reject);
        */
        let url;

        console.log("in geocode");
        if ( type === 'default') {
            console.log("default auto geo location query")

            url = "https://api.openweathermap.org/data/2.5/onecall?"
            url += "lat="+state.coords.lat+"&lon="+state.coords.long
            url += "&units=metric&appid=103e41e1f9fdd4e18a872b70f4a1c251";
        } else {
            console.log("searching for entered city")

            url = "https://api.openweathermap.org/data/2.5/weather?"
            url += "q=" + state.inputQuery 
            url += "&units=metric&appid=103e41e1f9fdd4e18a872b70f4a1c251";
      
        }
        console.log("geocode url="+ url);

        fetch(url)
          .then(response => {
              console.log("response=" + response)
            if (response.status !== 200) {
              commit('setAppStatus', {
                state: 'error',
                message: 'Uh oh, the geolocation API is not responding. Please try again.'
              })
              return
            }
            response.json().then(data => {
              if (!data.length) {
                commit('setAppStatus', {
                  state: 'error',
                  message: 'No results found. Please try another search.'
                })
                return
              }

              console.log("data=" + data[0])

              commit('setGeocode', data[0])
              //   resolve(response)
            })
          })
          .catch(() => {
            console.log("error fetching geocode");
            commit('setAppStatus', {
              state: 'error',
              message: 'Uh oh, the geolocation API is not responding.'
            })
          })
     //      })
    },

       weather ({commit, state}, type) {
         /*     return new Promise((resolve, reject) => {
        console.log("weather reject=" + reject);*/

        console.log("in weather state/index.js");
        let url;          
        if ( type === 'default') {
            console.log("default weather location query")

            url = "https://api.openweathermap.org/data/2.5/onecall?"
            url += "lat="+state.coords.lat+"&lon="+state.coords.lon
            url += "&units=metric&appid=103e41e1f9fdd4e18a872b70f4a1c251";
        } else {
            url = "https://api.openweathermap.org/data/2.5/weather?"
            url += "q=" + state.inputQuery 
            url += "&units=metric&appid=103e41e1f9fdd4e18a872b70f4a1c251";
      
        }
        console.log("weather url="+ url);



        fetch(url)
          .then(response => {
            if (response.status !== 200) {
              commit('setAppStatus', {
                state: 'error',
                message: 'Uh oh, the geolocation API is not responding. Please try again.'
              })
              return
            }
            response.json().then(data => {
              console.log("weather data: " + data)
              if (!data) {
                commit('setAppStatus', {
                  state: 'error',
                  message: 'No results found. Please try another search.'
                })
                return
              }
              console.log("setting weather data")
             let weather = {};

              if ( type === 'default' ){
                console.log("lat=" + data.lat + " lon=" + data.lon);
                weather.coords = {lat:data.lat,  lon:data.lon};
            weather.currentTemp = data.current.temp + "°C";
            weather.minTemp  = data.current.temp_min  + "°C";
            weather.maxTemp  = data.current.temp_max + "°C";
            weather.feelslike= data.current.feels_like + "°C";
            weather.pressure = data.current.pressure;
            weather.humidity = data.current.humidity + '%';
            weather.windspeed= data.current.wind_speed + 'm/s';
            weather.description     = data.current.weather[0].description;
            weather.icon     =  data.current.weather[0].icon + "@2x.png";
            weather.country  = data.sys.country;
/*            weather.sunrise  = new Date(data.current.sunrise*1000).toLocaleTimeString("en-GB");
            weather.sunset   = new Date(data.current.sunset*1000).toLocaleTimeString("en-GB");
            weather.time = new Date(data.current.dt*1000).toLocaleTimeString("en-GB");
            weather.tz = (data.timezone );

            let localTime = DateTime.local().setZone(data.timezone).toFormat('HH:mm');
            weather.localTime = localTime;

            console.log("local time = " + localTime);

            console.log("sunrise= " + weather.sunrise );
            console.log("sunset = " + weather.sunset);
*/
            weather.raw = data;
              } else { // user entered city

                weather.coords = {lat:data.coord.lat,  lon:data.coord.lon};

                weather.name = data.name;
                weather.country = data.sys.country;
            weather.currentTemp = data.main.temp + "°C";
            weather.minTemp  = data.main.temp_min + "°C";
            weather.maxTemp  = data.main.temp_max + "°C";
            weather.feelslike= data.main.feels_like + "°C";
            weather.pressure = data.main.pressure;
            weather.humidity = data.main.humidity + '%';
            weather.windspeed= data.wind.speed + 'm/s';
            weather.description= data.weather[0].description;
            weather.icon     =  data.weather[0].icon + "@2x.png";
/*            weather.sunrise  = new Date((data.sys.sunrise+data.timezone-3600)*1000).toLocaleTimeString("en-GB");
            weather.sunset   = new Date((data.sys.sunset+data.timezone-3600)*1000).toLocaleTimeString("en-GB");

            weather.time = new Date(data.dt*1000).toLocaleTimeString("en-GB");
            weather.tz = (data.timezone < 0 ) ?  "+" + (data.timezone/3600) : (data.timezone/3600);
            let x = Date.now() + ((data.timezone-3600) * 1000)  
            weather.tz2 = new Date(x).toLocaleTimeString("en-GB");
            console.log("time=" + x);
            console.log("tz2=" + weather.tz2)

            let localTime = DateTime.local().minus({minutes:0, seconds: data.timezone}).setZone(data.timezone).toFormat('HH:mm');
            weather.localTime = weather.tz2;

            console.log("local time = " + localTime);

*/


            weather.raw = data;

            }



              commit('setWeather', weather)
              //    resolve(response)
            })
          })
          .catch(() => {
            console.log("error getting weather data")
            commit('setAppStatus', {
              state: 'error',
              message: 'Uh oh, the geolocation API is not responding.'
            })
          })
         //      })
    }


  },
  modules: {
  }
})


