<template>

<div class="container">
  <div class="weather-side">
    <div class="weather-gradient"></div>
    <div class="date-container">
      <i class="location-icon" data-feather="map-pin"></i><h2 class="location">{{ store.weather.name }}</h2>
      <button 
        @click="switchComponent('options')"
        :disabled="currentComp==='options'"
        id="options_btn"></button>
    </div>
    <div class="weather-container"><i class="weather-icon" data-feather="sun"></i>
      <h1 class="weather-temp">{{ store.weather.currentTemp}}</h1>
      <h3 class="weather-desc">{{ store.weather.description}}</h3>

      <div class="today-info">
        <div class="feelslike"> <span class="title">Feels like</span><span class="value">{{ store.weather.feelslike }}</span>
          <div class="clear"></div>
        </div>
        <div class="precipitation"> <span class="title">Rain</span><span class="value">0 %</span>
          <div class="clear"></div>
        </div>
        <div class="humidity"> <span class="title">Humidity</span><span class="value">{{ store.weather.humidity}} </span>
          <div class="clear"></div>
        </div>
        <div class="wind"> <span class="title">Wind</span><span class="value">{{ store.weather.windspeed }}</span>
          <div class="clear"></div>
        </div>
      </div>
    </div>
  </div>



</div>
</template>

<script>
import { bus } from '../main.js'

export default {
data() {
  return {city: '', temperature:0}
}, 

props: {
    currentComp: {
      type: String,
      required: true
    }
},

mounted() {
    console.log("mounting search...");
    this.selectCity()
},
computed: {
  store() {
    return this.$store.state;
  }
},
methods: {
   switchComponent(comp) {
      bus.$emit('switchComp', comp);
   },
   
   selectCity(defCity) {
     let c = this.store.inputQuery;
     console.log("saved city is " + c);

     let query = (c === '' || c === undefined ) ? 'London' : c;
     console.log("Searching for : " + query + " def city="+defCity);

     this.$store.dispatch('inputQuery', query)
     console.log("setting app status in selectCity");
     this.$store.dispatch('appStatus', {state: 'loading'})
     console.log("getting weather in selectCity");
     this.$store.dispatch('weather')
         .then(() => this.$store.dispatch('appStatus', {state: 'loaded'}))
    }
 }
}
</script>



