<template>
  <div id="app">

  <div class="container">
    <component :is="currentComp"></component>
  </div>


   </div>
</template>

<script>
import Weather from './components/Weather.vue'
import Options from './components/Options.vue'
import { bus } from './main.js'


export default {
  name: 'App',
     data(){
    return {
      currentComp: 'weather'
    }
  },
  created(){
    bus.$on('switchComp', comp => {
      this.currentComp = comp;
    })
  },
  components: {
    Weather,
    Options
  }
}
</script>

